@import url(https://fonts.googleapis.com/css2?family=Gugi&family=Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif, 'Press Start 2P', 'Gugi';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background: linear-gradient(to left, tomato 0%, #fff 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.outerBox {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.outerBox span {
    font-family: 'Gugi';
    font-size: 4em;
    margin: 50px 0 50px 0;
}

.outerBox input {
    width: 300px;
    height: 50px;
    font-size: 2em;
    margin: 0 0 50px 0;
}

.myCardList{
    text-align: center;
    margin: 0 auto;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    display: inline-block;
}


.myCard
{
    text-align: center;
    background-color: #9eebcf;
    display: inline-block;
    border-width: .25rem;
    border-radius: .5rem;
    padding: 1rem;
    margin: .5rem;
    box-shadow: 4px 4px 8px 0 rgb(0 0 0 / 20%);
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;

}

.grow{
    -webkit-transform: translateZ( 0 );
            transform: translateZ( 0 );
    transition: -webkit-transform .25s ease-out;
    transition: transform .25s ease-out;
    transition: transform .25s ease-out, -webkit-transform .25s ease-out;
}

.grow:hover, .grow:focus { 
    -webkit-transform: scale( 1.05 ); transform: scale( 1.05 ); 
}
.grow:active { 
    -webkit-transform: scale( .90 ); transform: scale( .90 ); 
}

